.add-todo-container {
  position: sticky;
  top: 66px;
  padding: 0 10px 0 10px;
  margin-top: 48px;
  width: 100%;
  max-width: 540px;
  background: var(--background-color);
  box-shadow: 0 12px 12px 6px var(--background-color);
  z-index: 110;

  > form.add-todo {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 48px;

    > input {
      flex: 1;
      padding: 10px 16px;
      border: 3px solid var(--secondary-color);
      font-size: 16px;
      width: 100%;
      height: 100%;
      background-color: var(--gray-color-15);
      color: var(--text-color);

      &:focus {
        border-color: var(--input-focus-color);
        outline: none;
      }
      &::placeholder {
        color: var(--gray-color-11);
      }

      border-radius: 24px 9px 9px 24px;
      -webkit-border-radius: 24px 9px 9px 24px;
      -moz-border-radius: 24px 9px 9px 24px;
      -ms-border-radius: 24px 9px 9px 24px;
      -o-border-radius: 24px 9px 9px 24px;
    }

    > button {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-left: 3px;
      padding: 0;
      padding-right: 3px;
      border: 3px solid var(--secondary-color);
      background-color: var(--gray-color-14);
      font-size: 30px;
      color: #999999;
      height: 100%;
      width: 48px;
      cursor: pointer;

      &.active {
        border: 3px solid var(--secondary-color);
        background-color: var(--secondary-color);
        color: var(--gray-color-5);

        &:hover {
          border: 3px solid var(--gray-color-13);
          background-color: var(--gray-color-13);
        }
      }

      > span {
        font-size: 24px;
        font-weight: 700;
      }

      border-radius: 9px 24px 24px 9px;
      -webkit-border-radius: 9px 24px 24px 9px;
      -moz-border-radius: 9px 24px 24px 9px;
      -ms-border-radius: 9px 24px 24px 9px;
      -o-border-radius: 9px 24px 24px 9px;
    }
  }

  > p.add-todo-hint {
    margin: 6px 0 0 18px;
    font-size: 12px;
    color: #999;
    height: 16px;

    &.hidden {
      margin-top: -6px;
      opacity: 0;
    }
  }
}
